.signup_container {
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
}

.signup_form_container {
  width: 900px;
  height: 500px;
  display: flex;
  border-radius: 10px;
  box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
    0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.left {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #3c91e6;
  border-radius: 10px;
}

.left h1 {
  margin-top: 0;
  color: white;
  font-size: 35px;
  align-self: center;
}

.right {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.form_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form_container h1 {
  font-size: 40px;
  margin-top: 0;
}

.input {
  outline: none;
  border: none;
  width: 250px;
  padding: 15px;
  border-radius: 10px;
  background-color: #edf5f3;
  margin: 5px 0;
  font-size: 14px;
}
.radiobutton {
  display: flex;
  gap: 10px;
}
.input_container {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 1rem;
  flex-wrap: wrap;
}
.age,
.height,
.weight {
  width: 166px;
  padding: 15px;
  background-color: #edf5f3;
}

.error_msg {
  width: 370px;
  padding: 15px;
  margin: 5px 0;
  font-size: 14px;
  background-color: #f34646;
  color: white;
  border-radius: 5px;
  text-align: center;
}

.white_btn,
.green_btn {
  border: none;
  outline: none;
  padding: 12px 0;
  background-color: white;
  border-radius: 20px;
  width: 180px;
  font-weight: bold;
  font-size: 14px;
  cursor: pointer;
}

.green_btn {
  background-color: #3c91e6;
  color: white;
  margin: 10px;
}
@media screen and (max-width: 600px) {
  .signup_container {
    position: absolute;
    padding: 20px 20px;
    margin-top: -98px;
  }
  .signup_form_container {
    flex-wrap: wrap;
    }
    .left h1 {
      margin-top: 15px;
      color: white;
      font-size: 40px;
      align-self: center;
    }
  .right {
    padding: 20px 20px;
  }
  .input {
    width: 100%;
  }
  .age,
  .height,
  .weight {
    width: 100%;
    padding: 15px;
    background-color: #edf5f3;
  }
}
